body {
    margin: 0;
    padding: 0;
    font-family: Arial, sans-serif;
}

/* Navbar styles */
.navbar {
    background: #2883FF;
    height: 60px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 20px;
}

.navbar a {
    font-size: 18px;
    font-weight: 500;
    color: #ffffff;
    text-decoration: none;
    margin: 0 20px;
}

.navbar a img {
    width: 40px;
    height: 40px;
    padding: 5px;
}

/* Main content styles */
.cslr {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 20px;
    box-sizing: border-box;
    margin-bottom: 20px;
}

.text-section,
.image-section {
    flex: 1;
    padding: 20px;
    box-sizing: border-box;
}

.text-section h1 {
    color: #1E3164;
    font-family: Arial, sans-serif;
    font-weight: bold;
    font-size: 48px;
    margin-bottom: 10px;
}

.text-section p {
    color: #1E3164;
    font-family: Arial, sans-serif;
    font-size: 32px;
    margin-bottom: 20px;
}

.image-section img {
    width: 100%;
    max-width: 500px;
    height: auto;
}

.button {
    background: #2883FF;
    color: #ffffff;
    padding: 10px 20px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
}

/* Carousel styles */
.carousel-caption {
    position: absolute;
    top: 0;
    left: 10%;
    width: 90%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: start;
    text-align: left;
    background: rgba(0, 0, 0, 0.5);
    padding: 20px;
}

.carousel-caption h5 {
    font-size: 48px;
    font-family: Arial, sans-serif;
    font-weight: bold;
}

.carousel-caption p {
    font-family: Arial, sans-serif;
    font-weight: bold;
    font-size: 24px;
    color: #ffffff;
    margin-bottom: 20px;
}

.carousel-caption .circle {
    width: 54px;
    height: 54px;
    border-radius: 50%;
    margin-right: 10px;
}

/* Card styles */
.card-group {
    display: flex;
    justify-content: space-around;
    margin-bottom: 20px;
}

.card {
    flex: 0 0 30%;
    max-width: 30%;
    padding: 20px;
    box-sizing: border-box;
}

/* TV section styles */
.tv-section {
    margin: 50px 0;
    text-align: center;
}

.tv-section button {
    margin: 0 5px;
    width: 25%;
    padding: 10px;
}

/* Footer styles */
.ftco-footer {
    background: #333;
    color: #ffffff;
    padding: 50px 0;
    text-align: center;
}

/* Scroll to top button styles */
.mouse {
    position: fixed;
    right: 20px;
    bottom: 20px;
}

.mouse-icon {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 40px;
    height: 40px;
    background: #2883FF;
    border-radius: 50%;
    text-decoration: none;
    transition: background 0.3s ease;
}

.mouse-icon:hover {
    background: #1E3164;
}

.mouse-wheel {
    color: #ffffff;
    font-size: 24px;
}

/* Responsive styles for mobile */
@media (max-width: 767.98px) {
    .navbar {
        flex-direction: column;
        align-items: flex-start;
        padding: 10px;
    }

    .navbar a {
        margin: 10px 0;
    }

    .cslr {
        flex-direction: column;
        align-items: flex-start;
        padding: 10px;
    }

    .text-section,
    .image-section {
        padding: 10px;
    }

    .carousel-caption {
        left: 0;
        width: 100%;
        text-align: center;
    }

    .carousel-caption h5,
    .carousel-caption p {
        font-size: 32px;
    }

    .card-group {
        flex-direction: column;
    }

    .card {
        flex: 0 0 100%;
        max-width: 100%;
        margin-bottom: 20px;
    }

    .tv-section button {
        width: 100%;
        margin: 10px 0;
    }
}
